import Scroll from "../../../base/scroll/scroll.vue"
import Step from "../../../base/step/step.vue"
import Upload from "../../../base/upload/upload.vue"

export default {
  name: "getPrice4",
  components: {
    Step,
    Upload,
    Scroll
  },
  data() {
    return {
      stepArr: ["","", "", ""],
      stepIndex: 3,
      isOk: true, //
      orderImgs: [], //
      orderNum: '',
      realPay: '',
      type: this.$route.query.type
    }
  },
  methods: {
    doNext() {
      //var buyerTaskRecordId = localStorage.getItem("buyerTaskRecordId")
      let that = this;
      if(this.orderImgs.length === 0 || this.orderNum.length === 0 || this.realPay.length === 0) {
        this.$vux.alert.show({
          title:'',
          content: ''
        });
        return false
      }
      this.$axios.post('/api/orderOperate/bitslapOrderSubmit', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
        favImg: that.$route.query.obj.favImg,
        focusImg: that.$route.query.obj.focusImg,
        chatImg: that.$route.query.obj.chatImg,
        orderImgs: this.orderImgs,
        orderNum: this.orderNum,
        realPay: this.realPay
      }).then((data) => {
        console.log(data)
        if(data.data.code === '200') {
          this.$router.push({ name: 'submitSuccess', query: { type: 'price' } })
        } else {
          this.$vux.alert.show({
            title: '',
            content: data.data.message,
          })
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    }
  }
}



